import Vue from 'vue';
import App from '@/App';
import Router from 'vue-router'
import Test from '@/view/test.vue'; //榜单详情

import 'html5-reset/assets/css/reset.css'

Vue.config.productionTip = false
Vue.use(Router)

var router = new Router({
  // mode: 'history',
  // base: '/test.html',
  routes: [
    {
      path: '/',
      name: 'Test',
      component: Test
    }
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App />'
})


